import { Port } from "@/lib/Port";
import { Logger } from "@/lib/Logger";

export class Plotter {
  port: Port = new Port();

  public async connect(): Promise<void> {
    this.port.receiveCallback = this.receiveData;
    await this.port.open();
    this.port.readPort();
  }

  public async sendMove(dir: number): Promise<void> {
    const command = new Uint8Array([PlotterCommands.LINEAR_TRAVERSE, dir, 1]);
    this.port.write(command);
  }

  async receiveData(data: Uint8Array): Promise<void> {
    const command = data[0];

    switch (command) {
      case PlotterCommands.LINEAR_TRAVERSE:
        {
          Logger.info("Linear Command Received from Plotter");
        }
        break;
      default: {
        Logger.error("Unknown response from Plotter");
        Logger.error(data);
      }
    }
  }
}

export enum PlotterCommands {
  LINEAR_TRAVERSE = 0x01,
}

export enum PlotterDirections {
  LEFT = 0,
  UP = 1,
  RIGHT = 2,
  DOWN = 3,
}
